@media (max-width: 1042px){
    .footer__container {
    grid-template-columns: repeat(8, minmax(0, 94px));
    grid-template-areas: "logo logo logo text text text soc soc";
    }
    .footer__logo .logo__img {
        width: 166px;
        height: 26.79px;
      }
}


@media (max-width:786px){
    .footer__container {
            grid-template-columns: repeat(6, minmax(0, 94px));
            grid-template-areas: "logo logo logo soc soc soc";
      }
      .footer__logo .logo__img {
        width: 166px;
        height: 26.79px;
      }
      .footer__text {
        display: none;
      }
}
@media (max-width: 560px) {
    .footer {
        background: #202020;
      }
      .footer__container {
        display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: auto auto;
        gap: 0;
            grid-template-areas: "soc" "logo";
        margin: 0;
        padding: 0;
      }
      .footer__logo {
        display: grid;
        justify-items: center;
        width: 100%;
        padding: 13px;
        background: #4A4A4A;
      }
      .footer__logo .logo__img {
        margin-bottom: 4px;
        width: 111.55px;
        height: 18px;
      }
      .footer__logo .logo__descr {
        font-size: 10px;
        line-height: 13px;
      }
      .footer__social {
            justify-self: center;
        padding: 31px 0 28px;
      }
}