@media (max-width: 1042px) {
  .about__form{
    grid-template-columns:repeat(4,1fr);
  }
  .about__input{
    grid-column: 4 span;
  }

  .about__container {
    padding-top: 30px;
    padding-bottom: 81px;
}
}
@media (max-width: 786px) {
.about__container {
  padding-top: 30px;
  padding-bottom: 60px;
}
}
@media (max-width: 560px) {
    .about__list{
        grid-template-columns: 1fr;
    }
    .about__p {
      font-family: 'Roboto';
font-size: 14px;
font-weight: 400;
line-height: 25px;
letter-spacing: 0em;
text-align: left;

  }

  .about__h2 {
    font-size: 21px;
    font-weight: 700;
    line-height: 31px;
    letter-spacing: 0em;
    text-align: left;
}
.about__container {
  padding-top: 25px;
  padding-bottom: 45px;
}
}