@media (max-width: 1042px) {
    .header__logo{
        width: 166px;
    }
}
@media (max-width: 884px) {
    .header__nav--top{
        display: none;
    }

    .header__container{
        padding-top: 26px;
        padding-bottom: 25px;
    }

    .header__container--bottom{
        display: block;
    }

    .header__container {
        grid-template-columns: repeat(12, minmax(0, 80px));
        grid-template-areas: "logo logo logo logo logo logo search search search search search search ";
    }

}

@media (max-width:510px){
    .header__nav--bottom{
        display: none;
    }

    .btn__burger--open{
        display: block;
    }

    .header__container {
        grid-template-columns: repeat(4, minmax(0, 80px));
        grid-template-areas: "logo logo logo logo logo logo search search ";
    }

    .header__logo {
        width: 111px;
    }

    .header__container {
        padding-top: 21px;
        padding-bottom: 21px;
    }
}

