@media (max-width: 1042px) {
    .project__left-content{
        padding: 30px;
    }
    .project__right-h2{
        margin-bottom: 25px;
    }
    .project__container{
        padding-top: 37px;
    }

    .project__left-h2 {
        font-size: 34px;
        font-weight: 700;
        line-height: 37px;
        letter-spacing: 0em;
        text-align: left;
        margin-bottom: 19px;
    }
}

@media (max-width: 786px) {
    .project__right-h2 {
    margin-bottom: 14px;
    font-size: 22px;
    font-weight: 700;
    line-height: 23px;
    letter-spacing: 0em;
    text-align: left;
    }

    .project__left-h2{
font-size: 34px;
font-weight: 700;
line-height: 31px;
letter-spacing: 0em;
text-align: left;
    }
}

@media (max-width: 560px) {
    .project__container{
        grid-template-columns: 1fr;
    }

    .project__container {
        padding-top: 29px;
        padding-bottom: 52px;
    }

    .project__right-h2 {
        margin-bottom: 10px;
font-size: 18px;
font-weight: 700;
line-height: 19px;
letter-spacing: 0em;
text-align: left;

    }

    .project__left-h2{
font-size: 24px;
font-weight: 700;
line-height: 26px;
letter-spacing: 0em;
text-align: left;

    }
}

