@media (max-width: 1042px){
    .works__container {
    grid-template-columns: repeat(8, minmax(0, 94px));
    gap: 24px 30px;
}
.works__1{
    grid-column: 2 span;
}

.works__2{
    grid-column: 2 span;
}

.works__3{
    grid-column: 4 span;
}

.works__4{
    grid-column: 5 span;
}

.works__7{
    grid-column: 5 span;
}

.works__5{
    grid-column: 3 span;
    order: initial;
}

.works__6{
    grid-column: 3 span;
    order: initial;
}

.works__desk{
    margin-top: 15px;
}

.works__content-bg{
    padding: 30px 30px 34px;
}

.works__img {
    margin-bottom:0;
}
}
@media (max-width: 786px) {
    .works__container {
        grid-template-columns: repeat(6, minmax(0, 94px));
        gap: 27px 30px;
    }
    .works__3{
        grid-column: 2 span;
    }
    
    .works__4{
        grid-column: 6 span;
    }
    
    .works__7{
        grid-column: 6 span;
    }
    .works__desk{
        margin-bottom: 15px;
    }
    
  
}

@media (max-width: 560px) {
    .works__1{
        grid-column: 6 span;
    }
    
    .works__2{
        grid-column: 6 span;
    }
    
    .works__3{
        grid-column: 6 span;
    }
    
    .works__4{
        grid-column: 6 span;
    }
    
    .works__7{
        grid-column: 6 span;
    }
    
    .works__5{
        grid-column: 6 span;
        order: initial;
    }
    
    .works__6{
        grid-column: 6 span;
        order: initial;
    }

    .works__btn-ps{
        margin-top: 28px;
    }

    .works {
        padding-top: 50px;
        padding-bottom: 68px;
    }

    .works__h2{
font-size: 18px;
font-weight: 700;
line-height: 23px;
letter-spacing: 0em;
text-align: left;

    }

    .works__desk {
        margin-bottom: 10px;
    }
}
