@media (max-width: 1042px){

    .contacts__map{
        height: 460px;
        grid-column: 5 span;
    }

    .container__contacts{
        grid-template-columns: repeat(8, minmax(0, 94px));
    }

    .form__contacts{
        grid-column:3 span; 
    }

    .cont-more{
        top: initial;
    bottom: 0;
    left: 0;
    width: 100%;
    height: auto;
    }

    .cart-cont{
        position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    padding: 25px 0 21px 20px;
    }
}

@media (max-width: 884px) {
    .yandex-map{
        max-width: 850px;
        height: 460px;
    }
    .contacts__map{
        max-width: 850px;
        height: 460px;
        grid-column: 3 span;
    }
    .form__contacts{
        grid-column: 2 span; 
    }

    .container__contacts{
        grid-template-columns: repeat(3,1fr);
    }

    .contacts {
        padding-bottom: 50px;
    }
}

@media (max-width: 560px) {
    .form__contacts{
        grid-column: 3 span; 
    }
}